"use client";

import { useRef, useEffect } from "react";
import Image from "next/image";
import { Linkedin, ChevronLeft, ChevronRight } from "lucide-react";

interface Member {
  id: number;
  photo: string;
  name: string;
  role: string;
  linkedin?: string;
}

const data: Member[] = [
  { id: 1, photo: "/team/sachin.jpg", name: "Dr. Sachin Ahuja", role: "BDS, MDS (Orthodontics)" },
  { id: 2, photo: "/team/ankit.jpg", name: "Dr. Ankit Goyal", role: "BDS, MDS (Orthodontics)" },
  {
    id: 3,
    photo: "/team/himanshu.jpg",
    name: "Dr. Himanshu Kalia",
    role: "Scientific Consultant – BDS, MDS, MSc Oral Sciences",
    linkedin: "https://www.linkedin.com/in/hkalia1/",
  },
  { id: 4, photo: "/team/meenal.jpg", name: "Dr. Meenal Ahuja", role: "Clinical Expert – Endodontics" },
  { id: 5, photo: "/team/arzoo.jpg", name: "Dr. Arzoo Goyal", role: "Oral & Maxillofacial Surgery" },
  { id: 6, photo: "/team/vishal.jpg", name: "Mr. Vishal Kalia", role: "Business Development Consultant" },
  { id: 7, photo: "/team/savita.jpg", name: "Ms. Savita Sharma", role: "Outreach & Strategic Development" }, 
  { id: 8, photo: "/team/francesco.jpg", name: "Mr. Francesco Pra Mio", role: "Investor and Advisor" },

];

// Must match card styling
const CARD_WIDTH = 230;
const GAP = 20;
const STEP = CARD_WIDTH + GAP;

// Duplicate data for infinite loop
const loopData = [...data, ...data, ...data];

export default function Team() {
  const containerRef = useRef<HTMLDivElement>(null);
  const isJumping = useRef(false);

  // Start in the middle set
  useEffect(() => {
    const el = containerRef.current;
    if (!el) return;

    const setWidth = STEP * data.length;
    el.scrollLeft = setWidth;
  }, []);

  const scrollOneCard = (dir: "left" | "right") => {
    const el = containerRef.current;
    if (!el) return;

    el.scrollBy({
      left: dir === "right" ? STEP : -STEP,
      behavior: "smooth",
    });
  };

  const handleScroll = () => {
    if (isJumping.current) return;

    const el = containerRef.current;
    if (!el) return;

    const setWidth = STEP * data.length;

    // Jump only when clearly outside middle zone
    if (el.scrollLeft < setWidth * 0.5) {
      isJumping.current = true;
      el.scrollLeft += setWidth;
      requestAnimationFrame(() => (isJumping.current = false));
    }

    if (el.scrollLeft > setWidth * 1.5) {
      isJumping.current = true;
      el.scrollLeft -= setWidth;
      requestAnimationFrame(() => (isJumping.current = false));
    }
  };

  return (
    <section className="py-16 px-6 md:px-16 bg-[#f0f4f8]">
      <div className="text-center mb-12">
        <h2 className="text-4xl md:text-5xl font-extrabold relative inline-block">
          Our Advisors
          <span className="absolute left-0 bottom-2 w-full h-2 bg-blue-300 opacity-40 rounded" />
        </h2>
      </div>

      {/* Carousel */}
      <div
        ref={containerRef}
        dir="ltr"
        onScroll={handleScroll}
        className="flex gap-5 overflow-x-auto pb-4 no-scrollbar cursor-pointer"
      >
        {loopData.map((m, i) => (
          <div
            key={`${m.id}-${i}`}
            className="w-[230px] flex-none bg-white border border-gray-200 rounded-xl shadow-md"
          >
            <div className="relative h-[140px] w-full rounded-t-xl overflow-hidden bg-gray-50 cursor-pointer">
              <Image
                src={m.photo}
                alt={`${m.name} photo`}
                fill
                className="object-contain"
              />
            </div>

            <div className="p-3 text-center">
              <div className="flex justify-center items-center gap-2 mb-1">
                <h3 className="font-semibold text-sm">{m.name}</h3>
                {m.linkedin && (
                  <a
                    href={m.linkedin}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="bg-blue-600 text-white p-1 rounded-full"
                  >
                    <Linkedin size={12} />
                  </a>
                )}
              </div>
              <p className="text-xs italic text-gray-500">{m.role}</p>
            </div>
          </div>
        ))}
      </div>

      {/* Controls */}
      <div className="flex justify-center gap-4 mt-8">
        <button
          onClick={() => scrollOneCard("left")}
          className="bg-white shadow-md p-3 rounded-full hover:bg-gray-200"
          aria-label="Previous"
        >
          <ChevronLeft size={20} />
        </button>

        <button
          onClick={() => scrollOneCard("right")}
          className="bg-white shadow-md p-3 rounded-full hover:bg-gray-200"
          aria-label="Next"
        >
          <ChevronRight size={20} />
        </button>
      </div>

      {/* Hide scrollbar */}
      <style jsx>{`
        .no-scrollbar::-webkit-scrollbar {
          display: none;
        }
        .no-scrollbar {
          scrollbar-width: none;
          -ms-overflow-style: none;
        }
      `}</style>
    </section>
  );
}
