"use client";
import Image from "next/image";

import { useState } from "react";

export default function Navbar() {
  const [open, setOpen] = useState(false);
  return (
    <nav className="absolute top-0 left-0 right-0 z-30 w-full px-4 md:px-10 py-4 md:py-6 flex flex-col md:flex-row md:justify-between md:items-center">
      <div className="flex w-full md:w-auto justify-between items-center">
        {/* Logo */}
        <div className="flex items-center gap-2">
          <div className="flex items-center justify-center">
            <svg width="32" height="32" viewBox="0 0 24 24">
              <circle cx="6" cy="9" r="1.5" fill="#90cae2ff" />
              <circle cx="12" cy="9" r="1.5" fill="#90cae2ff" />
              <circle cx="18" cy="9" r="1.5" fill="#90cae2ff" />
              <path
                d="M6 14c1.7 2 4 3 6 3s4.3-1 6-3"
                stroke="#28A8E0"
                strokeWidth="2"
                fill="none"
                strokeLinecap="round"
              />
            </svg>
          </div>
          <span className="text-black text-2xl font-semibold tracking-tight">
            BraceFree<span className="text-[10px] align-top ml-[2px]">™</span>
          </span>
        </div>
        {/* Hamburger for mobile */}
        <button
          className="md:hidden ml-2 p-2 rounded focus:outline-none focus:ring-2 focus:ring-blue-400"
          onClick={() => setOpen((v) => !v)}
          aria-label="Toggle menu"
        >
          <svg width="28" height="28" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" d="M4 6h16M4 12h16M4 18h16" />
          </svg>
        </button>
      </div>
      {/* Navigation Menu */}
      <div
        className={`flex-col md:flex-row md:flex gap-10 text-white text-lg font-medium tracking-wide w-full md:w-auto bg-black md:bg-transparent rounded-b-xl md:rounded-none mt-2 md:mt-0 transition-all duration-300
        ${open ? 'flex' : 'hidden'} md:flex`}
      >
        <button className="hover:text-blue-300 transition cursor-pointer py-2 md:py-0 w-full md:w-auto text-left md:text-center px-4 md:px-0">Home</button>
        <button className="hover:text-blue-300 transition cursor-pointer py-2 md:py-0 w-full md:w-auto text-left md:text-center px-4 md:px-0">About</button>
        <button className="hover:text-blue-300 transition cursor-pointer py-2 md:py-0 w-full md:w-auto text-left md:text-center px-4 md:px-0">Our Vision</button>
        <button className="hover:text-blue-300 transition cursor-pointer py-2 md:py-0 w-full md:w-auto text-left md:text-center px-4 md:px-0">FAQs</button>
      </div>
    </nav>
  );
}
